import Vue from 'vue'
import VueRouter from 'vue-router'
import pathArr from '@/router/pathArr.js'

//導入需要的組件
import Login from '@/components/user/MyLogin.vue'
import Home from '@/components/user/MyHome.vue'

import Users from '@/components/menus/MyUsers.vue'
import Right from '@/components/menus/MyRights.vue'
import Goods from '@/components/menus/MyGoods.vue'
import Orders from '@/components/menus/MyOrders.vue'
import Setting from '@/components/menus/MySettings.vue'
import UserDetail from '@/components/user/MyUserDetail.vue'

// import axiosphp from '@/components/menus/axios.vue'


// import axios from 'axios'
// import vueAxios from 'vue-axios'
// Vue.use(axios, vueAxios)

Vue.use(VueRouter)

const router = new VueRouter({
    routes: [
        //登錄的路由規則
        // { path: '/', redirect: '/login ' },
        // 相同路由加載出現空白頁，改成只有一個
        { path: '/', component: Login },
        //登錄的路由規則
        // { path: '/login', component: Login },
        //後台主頁的路由規則
        {
            path: '/home',
            component: Home,
            //默認頁面
            //重對向
            redirect: '/home/users',
            children: [
                // { path: 'axiosphp', component: axiosphp },
                { path: 'users', component: Users },
                { path: 'rights', component: Right },
                { path: 'goods', component: Goods },
                { path: 'orders', component: Orders },
                { path: 'settings', component: Setting },
                //用戶詳情頁的路由控制
                { path: 'userinfo/:id', component: UserDetail, props: true }
            ]
        }
    ]
})

//路由的全局守衛
//如果網址直接輸入/home就判斷
//全域過濾
router.beforeEach(function(to, from, next) {
    //加入要判斷的網址

    //1.這裡定義
    // const pathArr = ['/home', '/home/users', '/home/rights']
    //2.或封裝方式另存網頁再用導入的方式

    // 1.if (to.path === '/home') {}
    // 2.if (to.path === '/home' || to.path === '/home/users') {}
    if (pathArr.indexOf(to.path) !== -1) {

        const token = localStorage.getItem('token')
        if (token) {
            next()
        } else {
            next('/login')
        }
    } else {
        next()
    }
})

export default router