<template lang="">
    <div class="login-container">
        <div class="login-box">
            <!-- 頭像區域 -->
            <div class="text-center avatar-box">
                <img src="../../assets/tter.jpg" class="img-thumbnail">
            </div>
            <!-- 表單區域 -->
            <div class="form-login p-4"></div>
            <!-- 登錄名稱 -->
             <div class="from-group form-inline">
                 <label for="username">登錄名稱</label>
                 <!-- v-model.trim //.trim去掉空格 -->
                 <input type="text" class="form-control ml-2" id="username" placeholder="請輸入登錄名稱" autocomplete="off" v-model.trim="username">
             </div>
            <!-- 登錄密碼 -->
            <div class="form-group form-inline">
                <label for="password">登錄密碼</label>
                <input type="password" class="form-control ml-2" id="password" placeholder="請輸入登錄密碼" v-model.trim="password">
            </div>
            <!-- 登錄和重置按鈕 -->
            <div class="form-group form-inline d-flex justify-content-end">
                <button type="button" class="btn btn-secondary mr-2" @click="reset">重置</button>
                <button type="button" class="btn btn-primary" @click="login">登錄</button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'MyLogin',
    data(){
        return{
            block:false,
            username:'',
            password:''
        }
    }, 
    methods:{
            reset(){
                this.username=''
                this.password=''
            },
            login(){
                if(this.username==='admin' && this.password==='123456'){
                    //登錄成功
                    //1.儲存token
                    //系統設定是Bearer空格 什麼什麼
                    localStorage.setItem('token','Bearer xxxx')
                    //2.跳轉到後台頁面
                    this.$router.push('/home')

                }else{
                    //登錄失敗
                    localStorage.removeItem('token')
               }  
            }
    } ,
    created(){
        // if(block===false){
        //自動f5無效
        // location.reload();
        // block=ture;
        // }else{
        // }
    }
}
</script>
<style lang="less" scoped>
    .login-container{
        // background-color: #2b6baf;
        width:600px;
        height:300px;
        .login-box{
            width:300px;
            height:250px;
            border-radius:3px;
            position:absolute;
            left:40%;
            background-color: #fff;
            margin-top:100px;
            padding:15px;
        }
    }

    .img-thumbnail{
        width:130px;
        height:100px;
    }
    
</style>